<template>
  <div id="ar-container">
    <img v-show="!showOperationTips" class="logo" src="../../../static/ar-logo.png">
    <div class="index-cotainer" v-show="!showOpenAR">
      <img class="index-landing-bg" src="https://staging-sapporobeer-cdn.augcloud.com/images/ar-landing.jpg" />
      <div class="btn-start-ar" @click="startAR" />
      <div class="tian-mao-store" @click="showStoreToken" />
    </div>
    <van-dialog class-name="store-link-container" v-model="showStoreLink" :show-confirm-button="false" close-on-click-overlay>
      <div class="store-link" :value="storeLink" v-text="storeLink" />
      <div class="copy" @click="copy" data-clipboard-target=".store-link" v-text="''" />
    </van-dialog>
    <van-overlay :show="showOverlay" class-name="overlay-container">
      <div class="overlay-after-played" v-show="showAfterPlayed">
        <div class="btn-record-story" @click="recordStory"></div>
        <img class="btn-record-story-img" src="https://staging-sapporobeer-cdn.augcloud.com/images/record-story.png">
        <div class="replay-container"><van-icon name="replay" size="0.8rem" class="replay-icon" @click="replay" /></div>
      </div>
      <div class="overlay-story" v-show="showStory">
        <swiper :options="storySwiperOption" ref="storySwiper" id="swiperContainer" :style="{ height: storySwiperHeight }">
          <swiper-slide>
            <img class="story-item" src="https://staging-sapporobeer-cdn.augcloud.com/images/story-item_1.png">
            <swiper :options="storyDetailSwiperOption" class="story-detail">
              <swiper-slide>
                <span class="story-text">150</span>
                <span class="story-text">SAPPORO1877 "SAPPORO"</span>
                <span class="story-text">SAPPOROFIRST BEER OF JAPAN</span>
                <span class="story-text"> 1964SAPPORO30SAPPORO</span>
              </swiper-slide>
              <div class="swiper-scrollbar" slot="scrollbar"></div>
            </swiper>
          </swiper-slide>
          <swiper-slide>
            <img class="story-item" src="https://staging-sapporobeer-cdn.augcloud.com/images/story-item_2.png">
            <swiper :options="storyDetailSwiperOption" class="story-detail">
              <swiper-slide>
                <span class="story-text"></span>
                <span class="story-text">  </span>
                <span class="story-text">SAPPORO</span>
              </swiper-slide>
              <div class="swiper-scrollbar" slot="scrollbar"></div>
            </swiper>
          </swiper-slide>
          <swiper-slide>
            <img class="story-item" src="https://staging-sapporobeer-cdn.augcloud.com/images/story-item_3.png">
            <swiper :options="storyDetailSwiperOption" class="story-detail">
              <swiper-slide>
                <span class="story-text">SAPPORO</span>
                <span class="story-text"> </span>
                <span class="story-text">SAPPORO  SAPPORO</span>
              </swiper-slide>
              <div class="swiper-scrollbar" slot="scrollbar"></div>
            </swiper>
          </swiper-slide>
          <swiper-slide>
            <img class="story-item" src="https://staging-sapporobeer-cdn.augcloud.com/images/story-item_4.png">
            <swiper :options="storyDetailSwiperOption" class="story-detail">
              <swiper-slide>
                <span class="story-text">   </span>
                <!-- <div class="space-line" /> -->
                <span class="story-text"> </span>
                <span class="story-text">30:70SAPPORO</span>
              </swiper-slide>
              <div class="swiper-scrollbar" slot="scrollbar"></div>
            </swiper>
          </swiper-slide>
          <swiper-slide>
            <img class="story-item" src="https://staging-sapporobeer-cdn.augcloud.com/images/story-item_5.png">
            <swiper :options="storyDetailSwiperOption" class="story-detail">
              <swiper-slide>
                <span class="story-text"> </span>
                <!-- <div class="space-line" /> -->
                <span class="story-text">SAPPOROSAPPORO</span>
              </swiper-slide>
              <div class="swiper-scrollbar" slot="scrollbar"></div>
            </swiper>
            <img class="btn-share-to-friends" @click="shareToFriends" src="../../../static/share-to-friends.png" />
          </swiper-slide>
        </swiper>
        <img class="arrow-left" @click="storySwiper.slideNext()" src="../../../static/left-arrow.png" />
      </div>
    </van-overlay>
    <van-overlay :show="showOperationTips" class-name="tip-overlay-container">
      <div class="tip-operation-container">
        <img class="close-btn" src="../../../static/tip-close.png" @click="close" />
        <div class="tip-operation">
          <img src="https://staging-sapporobeer-cdn.augcloud.com/images/tip-popup.png" />
          <div class="open-story" @click="close" />
        </div>
      </div>
    </van-overlay>
    <van-overlay class-name="tip-openinbrowser-container" :show="showCompatibilityTip">
      <img class="tip-openinbrowser" src="https://staging-sapporobeer-cdn.augcloud.com/images/tip-openinbrowser.png" />
    </van-overlay>
    <van-icon v-show="!showOperationTips" name="cross" size="1rem" class="close-icon" @click="close" />
  </div>
</template>

<script>
import { Dialog, Toast, Overlay, Icon } from 'vant'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
import Clipboard from 'clipboard'
import EventEmitter from '../../utils/eventEmitter'
import View from './view'

function getTaobaoStoreToken (cb) {
  const request = new XMLHttpRequest()
  request.open('get', 'https://staging-sapporobeer-cdn.augcloud.com/config.json')
  request.send(null)
  request.onload = () => {
    if (request.status === 200) {
      const json = JSON.parse(request.responseText)
      console.log(json)
      cb(json.taobaoStoreToken)
    }
  }
}

export default {
  name: 'AR',
  components: {
    [Dialog.Component.name]: Dialog.Component,
    'van-overlay': Overlay,
    'van-icon': Icon,
    swiper,
    swiperSlide
  },
  data: () => ({
    showOpenAR: false,
    showStoreLink: false,
    showOperationTips: false,
    showCompatibilityTip: false,
    storeLink: '',
    hasShowTip: false,
    showAfterPlayed: false,
    showStory: false,
    storySwiper: {},
    storySwiperOption: {
      width: window.innerWidth * 0.6,
      touchMoveStopPropagation: false
    },
    storyDetailSwiperOption: {
      direction: 'vertical',
      slidesPerView: 'auto',
      freeMode: true,
      scrollbar: '.swiper-scrollbar'
    },
    storySwiperHeight: `${window.innerWidth * 0.6 * 1743 / 718}px`
  }),

  computed: {
    showOverlay: function () {
      return this.showAfterPlayed || this.showStory
    }
  },

  created () {
    window.gtag('event', 'page_view', { page_title: 'AR landing ', content: 'AR landing ' })
    getTaobaoStoreToken((taobaoStoreToken) => { this.storeLink = taobaoStoreToken })
    this.eventEmitter = new EventEmitter()
    this.eventEmitter.on('mediaError', () => {
      this.showCompatibilityTip = true
      document.title = ''
    }, { once: true })
  },

  mounted () {
    this.canClickCloseIcon = true
    this.view = new View(this.eventEmitter, 'ar-container')
    this.eventEmitter.on('loopFinished', () => { this.showAfterPlayed = true }, false)
  },

  beforeDestroy () {
    console.log('beforeDestroy')
    this.view.dispose()
  },

  methods: {
    startAR: function () {
      window.gtag('event', 'page_view', { page_title: 'AR ', content: 'AR ' })
      window.gtag('event', 'click', { content: '' })
      this.showOpenAR = true
      if (!this.hasShowTip) {
        this.showOperationTips = true
        this.hasShowTip = true
        setTimeout(() => { this.view.play() }, 50)
      } else {
        this.view.play()
      }
    },
    copy: function () {
      const clipboard = new Clipboard('.copy')
      clipboard.on('success', () => {
        this.showStoreLink = false
        Toast('')
        window.gtag('event', 'click', { content: '' })
        clipboard.destroy()
      })
      clipboard.on('error', (e) => {
        console.error('Browser does not support, ', e)
        clipboard.destroy()
      })
    },
    close: function () {
      if (!this.canClickCloseIcon) {
        return
      }
      if (this.showOperationTips) {
        this.showOperationTips = false
        this.view.yuxi.startTrack()
      } else {
        if (this.showAfterPlayed) {
          this.showAfterPlayed = false
        } else if (this.showStory) {
          this.showStory = false
        }
        window.gtag('event', 'page_view', { page_title: 'AR landing ', content: 'AR landing ' })
        this.showOpenAR = false
        this.view.stop()
      }

      this.canClickCloseIcon = false
      setTimeout(() => { this.canClickCloseIcon = true }, 500)
    },
    recordStory: function () {
      window.gtag('event', 'click', { content: '' })
      window.gtag('event', 'page_view', { page_title: 'AR ', content: 'AR ' })
      this.showAfterPlayed = false
      this.showStory = true
      this.storySwiper = this.$refs.storySwiper.swiper
      delete this.$refs.storySwiper
    },
    replay: function () {
      window.gtag('event', 'click', { content: '' })
      this.showAfterPlayed = false
      this.view.yuxi.startTrack()
    },
    showStoreToken: function () {
      this.showStoreLink = true
      window.gtag('event', 'click', { content: '' })
    },
    shareToFriends: function () {
      window.gtag('event', 'click', { content: '' })
      this.$router.push({ name: 'post' })
    }
  }
}
</script>

<style scoped lang="less">
@import "./ar.less";
</style>



